import * as THREE from 'three'
import { controller } from '../AnimUtils/TweenUtils'
import { loadShader } from './ShaderLoader';

export interface CarouselOptions {
    numOfVisiblePics: number;
    planeWidth?: number;
    planeHeight?: number;
    planeGap?: number;
    carouselArrayRadius?: number
}

interface CarouselPlaneUniforms {
    vColor: { type: string, value: THREE.Color };
    vTextures: { type: string, value: Array<THREE.Texture | undefined> };
    vTextureMix: { type: string, value: number };
}

class CarouselPlane {
    index: number
    mesh: THREE.Mesh<THREE.PlaneBufferGeometry, THREE.ShaderMaterial>
    uniforms: CarouselPlaneUniforms
    textureList: Array<THREE.Texture | undefined>
    isPushingTexture: Boolean
    constructor(index: number, mesh: THREE.Mesh<THREE.PlaneBufferGeometry, THREE.ShaderMaterial>, uniforms: CarouselPlaneUniforms) {
        this.index = index
        this.mesh = mesh
        this.uniforms = uniforms
        this.mesh = mesh
        this.textureList = []
        this.isPushingTexture = false
    }
    pushNewTexture(newTexture: THREE.Texture) {
        if (this.textureList.length == 0) {
            this.textureList.push(undefined, newTexture)
        }
        else if (this.textureList.length == 1) {
            this.textureList.push(newTexture)
        }
        else {
            this.textureList.splice(1, this.textureList.length-2)
            this.textureList.push(newTexture)
        }
        if (this.isPushingTexture == false) {
            this.recPushTexture()
        }
    }
    recPushTexture() {
        if (this.textureList.length <= 1) {
            this.isPushingTexture = false
            return
        }
        this.isPushingTexture = true
        if (this.textureList.length == 2) {
            this.uniforms.vTextureMix.value = 0
            // this.uniforms.vTexture1.value = undefined
            // this.uniforms.vTexture2.value = undefined
            this.uniforms.vTextures.value[0] = this.textureList[0]
            this.uniforms.vTextures.value[1] = this.textureList[1]
            // this.textureList.splice(0, 1)
            this.isPushingTexture = false
            controller.tweenNumber(0, 1, (v) => {
                this.uniforms.vTextureMix.value = v
            },
            () => {
                this.uniforms.vTextures.value[0] = this.textureList[1]
                this.uniforms.vTextures.value[1] = this.textureList[1]
                this.uniforms.vTextureMix.value = 1
                this.textureList.splice(0, 1)
                this.recPushTexture()
            }, 500)
        }
    }
}

export class Carousel extends THREE.Group {
    numOfVisiblePics: number
    rotateNode: THREE.Group
    isRotating: Boolean
    planes: Array<CarouselPlane>
    planeWidth: number
    planeHeight: number
    planeGap: number
    preloadImgs: { [key: string]: THREE.Texture }
    maxPreloadImgCount: number
    carouselArrayRadius: number
    carouselArrayAngle: number
    currentCenter: number
    constructor(options: CarouselOptions = { numOfVisiblePics: 3 }) {

        super()

        this.isRotating = false
        this.rotateNode = new THREE.Group()
        this.add(this.rotateNode)
        this.currentCenter = 0

        this.numOfVisiblePics = options.numOfVisiblePics
        this.planes = []
        this.planeWidth = options.planeWidth || 50
        this.planeHeight = options.planeHeight || 50
        this.planeGap = options.planeGap || 10
        this.preloadImgs = {}
        this.maxPreloadImgCount = this.numOfVisiblePics * 2
        this.carouselArrayRadius = options.carouselArrayRadius || 100
        this.carouselArrayAngle = Math.atan((this.planeWidth + this.planeGap) / (this.carouselArrayRadius * 2)) * 2
        var [vs, fs] = loadShader('CarouselBasicShader')
        for (let i = 0; i < this.numOfVisiblePics; i++) {
            const tempUniforms = {
                vColor: { type: 'c', value: new THREE.Color(0xff0000) },
                vTextures: { type: 'tv', value: [ undefined, undefined ] },
                vTextureMix: { type: 'f', value: 1 },
            }
            const tempPlane = new THREE.Mesh(
                new THREE.PlaneBufferGeometry(this.planeWidth, this.planeHeight, 1, 1),
                new THREE.ShaderMaterial({
                    uniforms : tempUniforms,
                    vertexShader    : vs,
                    fragmentShader  : fs
                })
            )
            let offset = (i - (this.numOfVisiblePics - 1) / 2)
            tempPlane.rotation.y = -(offset * this.carouselArrayAngle)
            tempPlane.position.x = this.carouselArrayRadius * Math.sin(offset * this.carouselArrayAngle)
            tempPlane.position.z = -this.carouselArrayRadius * Math.cos(offset * this.carouselArrayAngle)
            this.planes.push(new CarouselPlane(i, tempPlane, tempUniforms))
            this.rotateNode.add(tempPlane)
        }
        this.planes.forEach(plane => {
            this.__imgSrcToTex(`https://picsum.photos/id/${Math.ceil(Math.random() * 50)}/200/200`).then((v) => {
                plane.pushNewTexture(v)
            })
        })
    }

    clickToChangeTex(raycaster: THREE.Raycaster, getImgSrcFunc: (carouselIndex: number) => string) {
        let imgMeshes = this.planes.map((v) => v.mesh)
        let intersects = raycaster.intersectObjects(imgMeshes)
        if (intersects.length == 1) {
            const intersectObj = intersects[0].object
            if (intersectObj instanceof THREE.Mesh) {
                let intersectIndex = imgMeshes.indexOf(intersectObj)
                let src = getImgSrcFunc(intersectIndex)
                this.__imgSrcToTex(src).then((v) => {
                    this.planes[intersectIndex].pushNewTexture(v)
                })
            }
        }
    }

    clickToRotate(raycaster: THREE.Raycaster, getImgSrcFunc: (carouselIndex: number) => string) {
        let imgMeshes = this.planes.map((v) => v.mesh)
        let intersects = raycaster.intersectObjects(imgMeshes)
        if (intersects.length == 1) {
            const intersectObj = intersects[0].object
            if (intersectObj instanceof THREE.Mesh) {
                let intersectIndex = imgMeshes.indexOf(intersectObj)
                let step = this.planes[intersectIndex].index - this.currentCenter - Math.floor(this.planes.length / 2)
                if (step < 0) {
                    this.moveLeft(-step, getImgSrcFunc)
                }
                else if (step > 0) {
                    this.moveRight(step, getImgSrcFunc)
                }
            }
        }
    }
    
    moveLeft(moveStep: number = 1, getImgSrcFunc: (carouselIndex: number) => string) {
        if (this.isRotating) {
            return
        }
        this.isRotating = true
        // move rightmost img to leftmost, change index, rotate rotateNode
        for (let i = 0; i < moveStep; i++) {
            this.planes.sort((v1, v2) => v2.index - v1.index)
            const rightmostImg = this.planes[0]
            rightmostImg.index = this.planes[this.planes.length - 1].index - 1
            let offset = (rightmostImg.index - (this.numOfVisiblePics - 1) / 2)
            rightmostImg.mesh.rotation.y = -(offset * this.carouselArrayAngle)
            rightmostImg.mesh.position.x = this.carouselArrayRadius * Math.sin(offset * this.carouselArrayAngle)
            rightmostImg.mesh.position.z = -this.carouselArrayRadius * Math.cos(offset * this.carouselArrayAngle)
            let src = getImgSrcFunc(rightmostImg.index)
            this.__imgSrcToTex(src).then((v) => {
                rightmostImg.pushNewTexture(v)
            })
        }
        controller.tweenNumber(0, moveStep, (v) => {
            let tempRotationPosition = this.carouselArrayAngle * (this.currentCenter - v)
            this.rotateNode.rotation.y = tempRotationPosition
        }, () => {
            this.currentCenter -= moveStep
            this.isRotating = false
        }, 500)
    }

    moveRight(moveStep: number = 1, getImgSrcFunc: (carouselIndex: number) => string) {
        if (this.isRotating) {
            return
        }
        this.isRotating = true
        // move leftmost img to rightmost, change index, rotate rotateNode
        for (let i = 0; i < moveStep; i++) {
            this.planes.sort((v1, v2) => v1.index - v2.index)
            const leftmostImg = this.planes[0]
            leftmostImg.index = this.planes[this.planes.length - 1].index + 1
            let offset = (leftmostImg.index - (this.numOfVisiblePics - 1) / 2)
            leftmostImg.mesh.rotation.y = -(offset * this.carouselArrayAngle)
            leftmostImg.mesh.position.x = this.carouselArrayRadius * Math.sin(offset * this.carouselArrayAngle)
            leftmostImg.mesh.position.z = -this.carouselArrayRadius * Math.cos(offset * this.carouselArrayAngle)
            let src = getImgSrcFunc(leftmostImg.index)
            this.__imgSrcToTex(src).then((v) => {
                leftmostImg.pushNewTexture(v)
            })
        }
        controller.tweenNumber(0, moveStep, (v) => {
            let tempRotationPosition = this.carouselArrayAngle * (this.currentCenter + v)
            this.rotateNode.rotation.y = tempRotationPosition
        }, () => {
            this.currentCenter += moveStep
            this.isRotating = false
        }, 500)
    }


    async __imgSrcToTex(imgSrc: string): Promise<THREE.Texture> {
        const self = this
        return new Promise<THREE.Texture>((resolve, reject) => {
            if (self.preloadImgs[imgSrc]) {
                resolve(self.preloadImgs[imgSrc])
            }
            let imageLoader = new THREE.ImageLoader()
            imageLoader.setCrossOrigin( 'anonymous' ) // TODO: debug
            imageLoader.load(imgSrc, (tempImageElement) => {
                let texture = new THREE.Texture( tempImageElement )
                texture.needsUpdate = true
                self.preloadImgs[imgSrc] = texture

                // dispose textures
                if (Object.keys(self.preloadImgs).length >= self.maxPreloadImgCount) {
                    // remove an unused texture
                    for (let k in self.preloadImgs) {
                        let imgTex = self.preloadImgs[k]
                        let canRemoveImg = true
                        for (let planeIndex = 0; planeIndex < self.planes.length; planeIndex ++) {
                            if (self.planes[planeIndex].uniforms.vTextures.value.indexOf(imgTex) > -1) {
                                canRemoveImg = false
                                break
                            }
                        }
                        if (canRemoveImg) {
                            // imgTex is not in use in any carousel card
                            imgTex.dispose()
                            delete self.preloadImgs[k]
                            break
                        }
                    }
                }
                resolve(texture)
            })
        })
    }
}