






























import * as THREE from 'three'
import Vue from 'vue'
import { FrontPageManager } from '../scripts/Three/FrontPage'

type Data = {
    fpm: FrontPageManager | null,
    loadingStat: {
        loaded: number,
        total: number,
        isLoaded: boolean,
        noLoadingStat: boolean
    }
}

export default Vue.extend({
    data(): Data {
        return {
            fpm: null,
            loadingStat: {
                loaded: 1,
                total: 2,
                isLoaded: false,
                noLoadingStat: false
            }
        }
    },
    mounted() {
        const self = this
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            this.fpm = new FrontPageManager(
                this.$refs['canvas-container'],
                () => {
                    console.log('loaded')
                    self.loadingStat.isLoaded = true
                    setTimeout(() => {
                        self.loadingStat.noLoadingStat = true
                    }, 501)
                },
                (_, loaded, total) => {
                    this.loadingStat.loaded = loaded
                    this.loadingStat.total = total
                },
                (url) => {
                    console.error('failed to load asset: ' + url)
                    self.loadingStat.isLoaded = true
                }
            )
            // ThreeGlassRefraction.initScene(this.$refs['canvas-container'])
            if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-glass'] instanceof HTMLElement) {
                const glassCSSObj = this.fpm.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-glass'], new THREE.Vector3(0, 20, 10))
                glassCSSObj.setFollowObject(this.fpm.glassMesh1Controller.object, new THREE.Vector3())
            }
            if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-test'] instanceof HTMLElement) {
                this.fpm.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-test'], new THREE.Vector3(-200, 110, 0))
            }
        }
    },
    methods: {
        onGlassClick() {
            alert('glass clicked!')
        }
    }
})
