import * as THREE from 'three'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { SQWater } from './SQWater'
// import { ClearPass } from 'three/examples/jsm/postprocessing/ClearPass'
// import FresnelShader from './FresnelShader'
import { Stats } from 'three-stats'
import { TardisCard, TardisCardShape } from './TardisCard'
import { Carousel } from './Carousel'
import { TDSLoader } from 'three/examples/jsm/loaders/TDSLoader'
import { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer } from './CustomCSS3DRenderer'
import { DampingObjectController, DampingVector3Controller } from './Utils'
import { RefractionObject, RefractionManager } from '../ThreeRealisticRefraction/ThreeRealisticRefraction'
import { RoundedBoxGeometry } from 'three/examples/jsm/geometries/RoundedBoxGeometry'
// import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry'
// import { Font } from 'three/examples/jsm/loaders/FontLoader'
// import { BrickManager } from './Brick'
import { controller } from '../AnimUtils/TweenUtils'
// import * as GEOM from '../GeomUtils'

class FrontPageManager {
    container: HTMLElement
    renderer: THREE.WebGLRenderer
    // controls: OrbitControls
    cardInsideScene: THREE.Scene
    scene: THREE.Scene
    reflectionScene: THREE.Scene
    composer: EffectComposer
    stats: Stats

    refractionManager: RefractionManager
    glassMesh1Controller: DampingObjectController

    // cursor position
    mousePosX: number
    mousePosY: number
    pointer: THREE.Vector2

    // camera
    camera: THREE.PerspectiveCamera
    cameraZPosition: number
    cameraPositionController: DampingVector3Controller
    cameraTargetPosition: THREE.Vector3
    cameraWithMouseMoveFactor: number
    cameraWithMouseRotateFactor: number

    // cursor light
    cursorLightGroup: THREE.Group
    cursorLight: THREE.PointLight
    cursorLightController: DampingObjectController

    // intersections
    raycaster: THREE.Raycaster
    intersectTardisCardObjs: Array<THREE.Object3D>
    intersectGlassObjs: Array<THREE.Object3D>

    // glass intersections
    glassSelected: THREE.Mesh | undefined
    glassIsRotating: Boolean

    // css
    cssScene: THREE.Scene
    cssRenderer: CustomCSS3DRenderer | undefined
    cssAttachObject: THREE.Mesh

    // carousel
    carousel1: Carousel
    carousel2: Carousel

    // loading manager
    loadingManager: THREE.LoadingManager

    constructor(_container: HTMLElement,
        onLoad?: () => void,
        onProgress?: (url: string, loaded: number, total: number) => void,
        onError?: (url: string) => void
        ) {

        // *********** basic settings ***********
        this.cameraZPosition = 500
        this.container = _container
        this.camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
        this.renderer = new THREE.WebGLRenderer( {alpha: true} )
        this.renderer.setClearColor( 0xffffff )
        this.renderer.setPixelRatio( window.devicePixelRatio )
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        this.renderer.domElement.style.position = 'absolute'
        this.renderer.domElement.style.top = '0px'
        this.renderer.domElement.style.left = '0px'
        this.container.appendChild( this.renderer.domElement )
        // TODO: (debug stats)
        this.stats = new Stats()
        this.stats.domElement.style.zIndex = '100'
        this.container.appendChild( this.stats.domElement )

        this.scene = new THREE.Scene()
        this.cssScene = new THREE.Scene()
        this.cardInsideScene = new THREE.Scene()
        this.reflectionScene = new THREE.Scene()

        // 
        this.loadingManager = new THREE.LoadingManager(() => {
            if (onLoad) {
                onLoad()
            }
        }, (url: string, loaded: number, total: number) => {
            if (onProgress) {
                onProgress(url, loaded, total)
            }
        }, (url) => {
            if (onError) {
                onError(url)
            }
        })

        // *********** background & env ***********
        const r = './cube1/'
        const urls = [
            r + 'px.jpg', r + 'nx.jpg',
            r + 'py.jpg', r + 'ny.jpg',
            r + 'pz.jpg', r + 'nz.jpg'
        ]
        const textureCube = new THREE.CubeTextureLoader(this.loadingManager).load( urls )
        textureCube.mapping = THREE.CubeRefractionMapping
        // this.scene.background = textureCube
        this.scene.environment = textureCube


        // *********** set light ***********
        var light = new THREE.DirectionalLight()
        light.intensity = 0.8
        light.position.x = 80
        light.position.y = 80
        light.position.z = 100
        this.scene.add(light)

        // *********** refraction objects ***********
        this.refractionManager = new RefractionManager(this.scene, this.container, this.renderer, this.camera)
        var glassMesh1 = new THREE.Mesh(
            new RoundedBoxGeometry(40, 40, 40, 5, 5),
            // new THREE.BoxGeometry(40, 40, 40),
            new THREE.MeshPhongMaterial({})
        )
        // raycast interaction agent with simple shape, to save calculation cost
        var glassMesh1InteractAgent = new THREE.Mesh(new THREE.BoxGeometry(40,40,40), new THREE.MeshBasicMaterial({}))
        glassMesh1InteractAgent.visible = false
        glassMesh1.add(glassMesh1InteractAgent)

        glassMesh1.rotation.set(1,1,1)
        this.refractionManager.addRefractionMesh(glassMesh1, true, null, 0x00ff00, 0x4060ff, 1.3, 0.8)
        this.glassMesh1Controller = new DampingObjectController(glassMesh1, 0.2, 0.1)


        // *********** composers ***********
        const renderPass1 = new RenderPass(this.scene, this.camera)
        renderPass1.clear = true
        const renderPass2 = new RenderPass(this.cardInsideScene, this.camera)
        renderPass2.clearDepth = true
        renderPass2.clear = false
        this.composer = new EffectComposer(this.renderer, new THREE.WebGLMultisampleRenderTarget(
            this.container.offsetWidth * window.devicePixelRatio,
            this.container.offsetHeight * window.devicePixelRatio
        ))
        // this.composer = new EffectComposer(this.renderer)
        this.composer.addPass(renderPass1)
        this.composer.addPass(renderPass2)
        this.composer.renderTarget1.stencilBuffer = true
        this.composer.renderTarget2.stencilBuffer = true


        // *********** tardis cards ***********

        var tardisCardTop = new TardisCard(100, 400, 110,
            this.scene, this.cardInsideScene, new THREE.Vector3(0, 200, 0), new THREE.Euler(), TardisCardShape.RECT,
            null)
        tardisCardTop.addSkybox(textureCube)
        var contentGroupInTardisCardTop = new THREE.Group()
        var titleTexture = new THREE.TextureLoader(this.loadingManager).load( './title.png' )
        var tardisCardTopTitle = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(390, 90, 1, 1),
            new THREE.MeshBasicMaterial({
                color: 0xffffff,
                alphaMap: titleTexture,
                alphaToCoverage: true,
                transparent: true,
            })
        )
        contentGroupInTardisCardTop.add(tardisCardTopTitle)
        contentGroupInTardisCardTop.position.copy(tardisCardTop.cardMesh.position)
        tardisCardTop.addVisibleObjectGroup(contentGroupInTardisCardTop)
        this.cssAttachObject = tardisCardTop.cardMesh
            
        var tardisCard1 = new TardisCard(200, 200, 111,
            this.scene, this.cardInsideScene, new THREE.Vector3(0, 0, 0), new THREE.Euler(), TardisCardShape.RECT,
            this.reflectionScene, 0xc0d0d0)
        
        var diffuseTexture = new THREE.TextureLoader(this.loadingManager).load( './marble/diffuse.jpg' )
        var normalTexture = new THREE.TextureLoader(this.loadingManager).load( './marble/normal.jpg' )
        var specTexture = new THREE.TextureLoader(this.loadingManager).load( './marble/specular.jpg' )
        var texs = [diffuseTexture, normalTexture, specTexture]
        texs.forEach(t => {
            t.wrapS = t.wrapT = THREE.RepeatWrapping
            t.repeat.set(1, 2)
        })
        var contentGroupInTardisCard1 = new THREE.Group()
        // https://github.com/mrdoob/three.js/blob/master/examples/webgl_loader_3ds.html
        const pgnormal = new THREE.TextureLoader(this.loadingManager).load( './jutabag3ds/texture/normal.jpg' )
        const loader = new TDSLoader(this.loadingManager);
        loader.setResourcePath('./jutabag3ds/texture/')
        loader.load( './jutabag3ds/portalgun.3ds', (obj) => {
            if (obj instanceof THREE.Group) {
                obj.traverse( function ( child ) {
                    if ( child instanceof THREE.Mesh ) {
                        console.log(child.material)
                        child.material.color = new THREE.Color(0xffffff)
                        // child.material.map = pgcolor
                        child.material.normalMap = pgnormal
                    }
                } )
                obj.scale.multiplyScalar(80)
                obj.position.x -= 20
                obj.position.y += 10
                obj.position.z -= 80
                obj.rotation.x = -2
                obj.rotation.z = 0.7
                contentGroupInTardisCard1.add(obj)
                
                var tardisCardBackground1 = new THREE.Mesh(
                    new THREE.PlaneBufferGeometry(500, 500, 1, 1),
                    new THREE.MeshPhysicalMaterial({
                        color: 0xccffff,
                        map: diffuseTexture,
                        normalMap: normalTexture,
                        normalScale: new THREE.Vector2(0.1, 0.1),
                        specularColorMap: specTexture,
                        specularIntensity: 4,
                        reflectivity: 1.0,
                        metalness: 0.2,
                        roughness: 0.1,
                        clearcoat: 0.5,

                    })
                )
                tardisCardBackground1.position.set(0, 0, -150)
                contentGroupInTardisCard1.add(tardisCardBackground1)
                contentGroupInTardisCard1.position.copy(tardisCard1.cardMesh.position)
                tardisCard1.addVisibleObjectGroup(contentGroupInTardisCard1)
            }
            // contentGroupInTardisCard1.add(obj)
        });

        
        var tardisCard2 = new TardisCard(100, 100, 112, this.scene, this.cardInsideScene, new THREE.Vector3(200, 0, 0), new THREE.Euler(), TardisCardShape.CIRCLE,
        this.reflectionScene, 0xd0c0d0)
        this.carousel1 = new Carousel({
            numOfVisiblePics: 5,
            planeWidth: 80,
            planeHeight: 80,
            carouselArrayRadius: 120
        })
        var diffuseTexture2 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop1_diffuse.png' )
        var normalTexture2 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop1_normal.png' )
        var specTexture2 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop1_specular.png' )
        texs = [diffuseTexture2, normalTexture2, specTexture2]
        var tardisCardBackground = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(500, 500, 1, 1),
            new THREE.MeshPhysicalMaterial({
                map: diffuseTexture2,
                normalMap: normalTexture2,
                specularColorMap: specTexture2,
                normalScale: new THREE.Vector2(0.4, 0.4),
                specularIntensity: 0.2,
                reflectivity: 0.1,
                metalness: 0.1,
                roughness: 0.4
            })
        )
        tardisCardBackground.position.set(0, 0, -150)
        // scene.add(contentInTardisCard)
        var contentGroupInTardisCard2 = new THREE.Group()
        contentGroupInTardisCard2.add(this.carousel1)
        contentGroupInTardisCard2.add(tardisCardBackground)
        contentGroupInTardisCard2.position.copy(tardisCard2.cardMesh.position)
        tardisCard2.addVisibleObjectGroup(contentGroupInTardisCard2)
        
        var tardisCard3 = new TardisCard(100, 100, 113, this.scene, this.cardInsideScene, new THREE.Vector3(-200, 0, 0), new THREE.Euler(), TardisCardShape.CIRCLE,
        this.reflectionScene, 0xa0c090)
        this.carousel2 = new Carousel({
            numOfVisiblePics: 4,
            planeWidth: 70,
            planeHeight: 70,
            carouselArrayRadius: 120
        })
        var diffuseTexture3 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop2_diffuse.png' )
        var normalTexture3 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop2_normal.png' )
        var specTexture3 = new THREE.TextureLoader(this.loadingManager).load( './painting/crop2_specular.png' )
        texs = [diffuseTexture3, normalTexture3]
        // texs.forEach(t => {
        //     t.wrapS = t.wrapT = THREE.RepeatWrapping
        //     t.repeat.set(4, 4)
        // })
        tardisCardBackground = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(500, 500, 1, 1),
            new THREE.MeshPhysicalMaterial({
                color: 0xffff80,
                map: diffuseTexture3,
                normalMap: normalTexture3,
                specularColorMap: specTexture3,
                normalScale: new THREE.Vector2(0.5, 0.5),
                specularIntensity: 0.2,
                reflectivity: 0.8,
                metalness: 0,
                roughness: 0.2,
            })
        )
        tardisCardBackground.position.set(0, 0, -150)
        var contentGroupInTardisCard3 = new THREE.Group()
        contentGroupInTardisCard3.add(this.carousel2)
        contentGroupInTardisCard3.add(tardisCardBackground)
        contentGroupInTardisCard3.position.copy(tardisCard3.cardMesh.position)
        tardisCard3.addVisibleObjectGroup(contentGroupInTardisCard3)


        // *********** water ***********
        const waterGeometry = new THREE.PlaneGeometry( 1000, 400 )
        const water = new SQWater( waterGeometry, this.reflectionScene, {
            // color: params.color,
            // scale: params.scale,
            flowDirection: new THREE.Vector2( 1, 1 ),
            textureWidth: 512,
            textureHeight: 512
        } );

        // water.position.y = 1;
        water.position.z += 200;
        water.position.y -= 110;
        water.rotation.x = Math.PI * - 0.5;
        this.scene.add( water );


        // *********** camera movement ***********
        this.cameraTargetPosition = new THREE.Vector3(0, 0, this.cameraZPosition)
        this.camera.position.copy(this.cameraTargetPosition)
        this.camera.lookAt(0, 0, 0)
        this.cameraWithMouseMoveFactor = 0.04
        this.cameraWithMouseRotateFactor = 0.015
        this.cameraPositionController = new DampingVector3Controller(this.camera.position, 0.5, 0.05)
        window.addEventListener('mousemove', e => {
            self.onMouseMove(e)
        })


        // *********** cursor light ***********
        this.cursorLightGroup = new THREE.Group()
        this.cursorLight = new THREE.PointLight()
        this.cursorLight.intensity = 1
        this.cursorLight.decay = 1
        this.cursorLight.distance = 200
        this.cursorLight.color = new THREE.Color( 0xaaccff )
        this.cursorLightGroup.add(this.cursorLight)
        this.scene.add(this.cursorLightGroup)
        this.cursorLightController = new DampingObjectController(this.cursorLightGroup, 0.3, 0.1)
        

        // *********** raycast intersections ***********
        this.raycaster = new THREE.Raycaster()
        this.intersectTardisCardObjs = []
        this.intersectTardisCardObjs.push(tardisCardTop.cardMesh, tardisCard1.cardMesh, tardisCard2.cardMesh, tardisCard3.cardMesh)
        this.intersectGlassObjs = []
        this.glassSelected = undefined
        this.glassIsRotating = false
        this.intersectGlassObjs.push(glassMesh1InteractAgent)


        // *********** events ***********
        this.mousePosX = 0
        this.mousePosY = 0
        this.pointer = new THREE.Vector2()
        const self = this
        window.addEventListener( 'resize', () => {
            self.onWindowResize()
        } )
        window.addEventListener( 'click', (e) => {
            self.onClick(e)
        } )


        // *********** final animate ***********
        this.animate()
    }

    animate = () => {
        requestAnimationFrame( this.animate )
        // damping controllers
        this.updatecornerObject()
        this.updateCamera()
        this.updateCursorLight()
        this.render()
        this.stats.update()
    }

    render()
    {
        this.composer.render()
        this.renderer.clearDepth()
        this.refractionManager.render()
        if (this.cssRenderer) {
            this.cssRenderer.render(this.cssScene, this.camera)
        }
    }

    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.refractionManager.onWindowResize()
        this.renderer.setSize( window.innerWidth, window.innerHeight )
        this.composer.setSize( window.innerWidth, window.innerHeight )
        if (this.cssRenderer) {
            this.cssRenderer.setSize( window.innerWidth, window.innerHeight )
        }
    }
    
    onMouseMove(e: MouseEvent) {
        this.mousePosX = e.clientX
        this.mousePosY = e.clientY
        this.pointer.x = ( e.clientX / window.innerWidth ) * 2 - 1;
		this.pointer.y = - ( e.clientY / window.innerHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.pointer, this.camera )
        const intersects = this.raycaster.intersectObjects( this.intersectGlassObjs, false )
        if (intersects.length == 1 && !this.glassIsRotating && this.glassSelected !== intersects[0].object.parent) {
            console.log('in')
            this.glassIsRotating = true
            const intersectObj = intersects[0].object.parent
            const refObj = intersectObj ? intersectObj['refractionObject'] : null
            if (intersectObj instanceof THREE.Mesh && refObj instanceof RefractionObject) {
                this.glassSelected = intersectObj
                controller.tweenEuler(this.glassSelected.rotation, new THREE.Euler(1,6,1), (v) => {
                        intersectObj.rotation.copy(v)
                    },
                    () => {this.glassIsRotating = false},
                    500
                )
                controller.tweenNumber(1, 0, (val) => {
                    refObj.refractionMaterialUniforms['absorbColor'].value.b = val
                },
                () => {},
                500)
            }
            // this.glassSelected.rotation.set(1,2,1)
        }
        else if (intersects.length == 0 && !this.glassIsRotating && this.glassSelected !== undefined) {
            console.log('out')
            this.glassIsRotating = true
            let rotateObj = this.glassSelected
            const refObj = this.glassSelected['refractionObject']
            controller.tweenEuler(this.glassSelected.rotation, new THREE.Euler(1,1,1), (v) => {
                rotateObj.rotation.copy(v)
                },
                () => {this.glassIsRotating = false},
                500
            )
            controller.tweenNumber(0, 1, (val) => {
                refObj.refractionMaterialUniforms['absorbColor'].value.b = val
            },
            () => {},
            500)
            this.glassSelected = undefined
        }
    }

    onClick(e: MouseEvent) {
        if (this.glassSelected) {
            return
        }
        const intersects = this.raycaster.intersectObjects( this.intersectTardisCardObjs, false )
        
        if (intersects.length == 1) {
            const intersectObj = intersects[0]
            controller.tweenThreeVector3(
                this.cameraTargetPosition,
                new THREE.Vector3(
                    intersectObj.object.position.x,
                    intersectObj.object.position.y,
                    this.cameraTargetPosition.z
                ),
                (updatedVec) => {
                    this.cameraTargetPosition.copy(updatedVec)
                },
                () => {},
                750
            )
            this.carousel1.clickToRotate(this.raycaster, (carouselIndex) => {
                console.log('new carousel img index', carouselIndex)
                return `https://picsum.photos/id/${Math.ceil(Math.random() * 50)}/200/200`
            })
            this.carousel2.clickToChangeTex(this.raycaster, (carouselIndex) => {
                console.log('new carousel img index', carouselIndex)
                return `https://picsum.photos/id/${Math.ceil(Math.random() * 50)}/200/200`
            })
            // change carousel
        }
    }

    getRaycastPos(posX: number, posY: number) {
        let x = (2.0 * posX) / window.innerWidth - 1.0
        let y = 1.0 - (2.0 * posY) / window.innerHeight
        // let z = 1.0
        // let ray_nds = new THREE.Vector3(x, y, z)
        let ray = new THREE.Vector3(x, y, 0.995)
        ray.unproject(this.camera)
        return ray
    }

    updatecornerObject() {
        let targetPos = this.getRaycastPos(window.innerWidth - 200, window.innerHeight / 2)
        // let lastPos = cornerObject.position
        this.glassMesh1Controller.update(targetPos)
        // let currentPath = targetPos.sub(lastPos)
        // cornerObjectVelocity.multiplyScalar(0.3)
        // cornerObjectVelocity.add(currentPath.multiplyScalar(damp))
        // cornerObject.position.add(cornerObjectVelocity)
    }

    updateCamera() {
        const relX = window.innerWidth / 2 - this.mousePosX
        const relY = window.innerHeight / 2 - this.mousePosY
        // console.log(this.cameraTargetPosition)
        const dampPos = (new THREE.Vector3(
            -relX * this.cameraWithMouseMoveFactor,
            relY * this.cameraWithMouseMoveFactor,
            0
        )).add(this.cameraTargetPosition)

        this.cameraPositionController.update(dampPos)
        var vel = this.cameraPositionController.velocity
        this.camera.rotation.x = vel.y * this.cameraWithMouseRotateFactor
        this.camera.rotation.y = -vel.x * this.cameraWithMouseRotateFactor
    }

    updateCursorLight() {
        if (!isNaN(this.mousePosX) && !isNaN(this.mousePosY)) {
            let targetPos = this.getRaycastPos(this.mousePosX, this.mousePosY)
            let camPosZ = this.camera.position.z
            let targetPosVec = new THREE.Vector3().copy(targetPos)
            targetPosVec = targetPosVec.sub(this.camera.position)
            let newz = -200
            let x = (newz - camPosZ) / targetPosVec.z
            targetPos.copy(this.camera.position)
            targetPosVec.multiplyScalar(x)
            targetPos.add(targetPosVec)
            targetPos.z += 100
            this.cursorLightController.update(targetPos)
            // console.log(targetPos)
        }
    }

    registerCSSCoordinator(htmlContainer: HTMLElement, htmlElem: HTMLElement, position: THREE.Vector3 = new THREE.Vector3(), scale: number = 1.0): CustomCSS3DObject {
        if (this.cssRenderer == undefined) {
            this.cssRenderer = new CustomCSS3DRenderer(htmlContainer)
            this.cssRenderer.setSize( window.innerWidth, window.innerHeight )
        }
        // controls = new OrbitControls(camera, cssRenderer.domElement)
        const object = new CustomCSS3DObject( htmlElem )
        object.position.copy(position)
        object.scale.multiplyScalar(scale)
        this.cssScene.add(object)
        return object
        // cssCoordinators.push(new ThreeCSSCoordinator(htmlContainer, htmlCamera, htmlElem, camera, position))
    }



    
}



export {
    FrontPageManager
}