import * as THREE from 'three'
import { loadShader } from './ShaderLoader'

export enum TardisCardShape {
    RECT,
    CIRCLE
}

export class TardisCard {
    cardGeometry: THREE.BufferGeometry
    cardMaterial: THREE.Material
    cardMesh: THREE.Mesh
    stencilId: number
    cardOutsideScene: THREE.Scene
    cardInsideScene: THREE.Scene
    isFocused: boolean
    height: number
    width: number
    frontWallGeom: THREE.BufferGeometry
    frontWallMesh: THREE.Mesh
    constructor(height: number = 100, width: number = 100,
        stencilId: number,
        cardOutsideScene: THREE.Scene, cardInsideScene: THREE.Scene,
        position: THREE.Vector3, rotation: THREE.Euler,
        shape: TardisCardShape = TardisCardShape.RECT,
        reflectionScene: THREE.Scene | null = null, reflectionColor: number = 0xff0000
        ) {
        this.stencilId = stencilId
        this.height = height
        this.width = width
        if (shape == TardisCardShape.RECT) {
            this.cardGeometry = new THREE.PlaneGeometry(width, height)
        }
        else {
            this.cardGeometry = new THREE.CircleGeometry(width, 64)
        }
        this.cardMaterial = new THREE.MeshBasicMaterial({
            color: 0xf0f0f0,
            // colorWrite: false,
            stencilWrite: true,
            stencilRef: stencilId,
            stencilZPass: THREE.ReplaceStencilOp,  // write if the depth buffer test passes
            depthWrite: false
        })
        this.cardMesh = new THREE.Mesh(this.cardGeometry, this.cardMaterial)
        this.cardMesh['tardis'] = this
        this.cardMesh.position.copy(position)
        this.cardMesh.rotation.copy(rotation)
        this.cardOutsideScene = cardOutsideScene
        this.cardInsideScene = cardInsideScene
        this.cardOutsideScene.add(this.cardMesh)
        this.isFocused = false
        this.frontWallGeom = new THREE.BufferGeometry()
        this.frontWallMesh = new THREE.Mesh()

        if (reflectionScene != null) {
            var cardReflectionMesh = new THREE.Mesh().copy(this.cardMesh)
            cardReflectionMesh.material = new THREE.MeshBasicMaterial({
                color: reflectionColor
            })
            reflectionScene.add(cardReflectionMesh)
        }
    }
    /**
     * add group of objects to card that acts as contents that COULD BE VISIBLE FROM THE OUTSIDE
     * @param group
     */
    addVisibleObjectGroup(group: THREE.Group) {
        this._recursiveGetGroupProperty(group, (cardMaterial) => {
            cardMaterial
            cardMaterial.stencilWrite = true
            cardMaterial.stencilRef = this.stencilId
            cardMaterial.stencilFunc = THREE.EqualStencilFunc
            // cardMaterial.depthTest = false
        })
        this.cardOutsideScene.add(group)
    }
    _recursiveGetGroupProperty( group: THREE.Group, setFunc: { (cardMat: THREE.Material): any } ) {
        group.children.forEach(v => {
            if (v instanceof THREE.Mesh) {
                if (v.material instanceof THREE.Material) {
                    setFunc(v.material)
                }
                else if (v.material instanceof Array) {
                    v.material.forEach(mat => {
                        setFunc(mat)
                    })
                }
            }
            else if (v instanceof THREE.Group) {
                this._recursiveGetGroupProperty(v, setFunc)
            }
        })
    }
    addFrontWall(thickness: number = 10, material: THREE.Material | undefined = undefined) {
        const positions = [
            new THREE.Vector3(-this.width/2, -this.height/2, 0),
            new THREE.Vector3(-this.width/2,  this.height/2, 0),
            new THREE.Vector3( this.width/2,  this.height/2, 0),
            new THREE.Vector3( this.width/2, -this.height/2, 0),
            new THREE.Vector3(-this.width/2, -this.height/2, -thickness),
            new THREE.Vector3(-this.width/2,  this.height/2, -thickness),
            new THREE.Vector3( this.width/2,  this.height/2, -thickness),
            new THREE.Vector3( this.width/2, -this.height/2, -thickness)
        ]

        const indices = [
            1, 0, 4,
            5, 1, 4,
            2, 1, 5,
            6, 2, 5,
            3, 2, 6,
            7, 3, 6,
            0, 3, 7,
            4, 0, 7
        ]

        const points: Array<THREE.Vector3> = []
        indices.forEach(i => {
            let tv = new THREE.Vector3()
            // tv.multiplyScalar(100)
            tv.copy(positions[i])
            points.push(tv)
        })
        this.frontWallGeom.setFromPoints(points)
        this.frontWallGeom.computeVertexNormals()
        const frontWallMat = (material == undefined) ? new THREE.MeshPhongMaterial({
            color: 0xa0a0a0,
        }) : material
        frontWallMat.stencilWrite = true
        frontWallMat.stencilRef = this.stencilId
        frontWallMat.stencilFunc = THREE.EqualStencilFunc
        this.frontWallMesh = new THREE.Mesh(
            this.frontWallGeom,
            frontWallMat
        )
        this.frontWallMesh.position.copy(this.cardMesh.position)
        this.cardOutsideScene.add(this.frontWallMesh)
    }
    addSkybox(textureCube: THREE.CubeTexture) {
        var [vs, fs] = loadShader('CardSkyboxShader')
        var uniforms = 
        {
            "tCube":            { type: "t", value: textureCube }
        }
        var customMaterial = new THREE.ShaderMaterial( 
            {
                uniforms:       uniforms,
                vertexShader:   vs,
                fragmentShader: fs,
                stencilWrite: true,
                stencilRef: this.stencilId,
                stencilZPass: THREE.ReplaceStencilOp,  // write if the depth buffer test passes
                depthWrite: false,
            }
        )
        this.cardMaterial = customMaterial
        this.cardMesh.material = this.cardMaterial
    }
}